  display: inline-block;
  position: relative;
}
.signup::before {
  content: "";
  display: block;
  position:absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: url("./../assets/signup-5.jpeg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  opacity: 0.8;
}

.title {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-style: oblique;
  font-size: 60px;
  text-align: center;
  color: black;
  margin-top: 30px;
}

.slack_account {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
  position: relatives;
  margin-left: 600px;
  margin-right: 600px;
}

.profile_name {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
  position: relatives;
  margin-left: 600px;
  margin-right: 600px;
}

.password_prompt {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
  color:rgb(240, 173, 79);
  position: relatives;
  margin-left: 600px;
  margin-right: 600px;
}

.VuePassword {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 25px;
  position: relatives;
  margin-left: 600px;
  margin-right: 600px;
}

  .VuePassword__Input {
    position: relative;
  }
  .VuePassword input {
    padding-right: 2.5em;
    width: 100%;
  }
  .VuePassword__Toggle {
    color: gray;
    display: inline-block;
    height: 100%;
    position: absolute;
